import ExtensionsHeader from "@components/extensions-header";
import { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { ChevronDown, Edit, MapPin, MoreVertical, Trash2 } from "react-feather";
import { FormattedMessage } from "react-intl";
import ReactPaginate from "react-paginate";
import { Link } from "react-router-dom";
import {
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  UncontrolledDropdown,
} from "reactstrap";

import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Button, Card, Col, Row } from "reactstrap";
import api from "../../../../../constants/api";
import request from "../../../../../services/request";
import Sidebar from "./add/index";
import XLSX from "xlsx";

const CustomHeader = ({ toggleSidebar, data, exportToExcel }) => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-1 mb-75">
      {
        <Row className="justify-content-end">
          <Col
            xl="6"
            className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column gap-3 pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <Button.Ripple color="primary" onClick={exportToExcel}>
              Export Excel
            </Button.Ripple>
            <Button.Ripple color="primary" onClick={toggleSidebar}>
              <FormattedMessage id="Add" />
            </Button.Ripple>
          </Col>
        </Row>
      }
    </div>
  );
};

const CountryList = () => {
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const store = useSelector((state) => state.abouts);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [count, setCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [disable, setDisable] = useState(false);
  const [listCountry, setListCountry] = useState([]);
  const [listCountryExport, setListCountryExport] = useState([]);
  const toggleSidebar = () => {
    setDisable(false);
    setSidebarOpen(!sidebarOpen);
  };

  const handleDelete = (data) => {
    request
      .send({
        method: api.UPDATE_COUNTRY.method,
        path: api.UPDATE_COUNTRY.path,
        data,
      })
      .then((result) => {
        if (result) {
          const { statusCode, data } = result;
          if (statusCode === 200) {
            toast.success(<FormattedMessage id={"Delete successfully!"} />);
            getCountry();
          } else if (statusCode == 400) {
            toast.warn(<FormattedMessage id={"Delete failed!"} />);
          }
        }
      });
  };
  const columns = [
    {
      name: <FormattedMessage id="id" />,
      minWidth: "258px",
      selector: "id",
      sortable: false,
      cell: (row) => row?.id,
    },

    {
      name: <FormattedMessage id="name" />,
      minWidth: "258px",
      selector: "name",
      sortable: false,
      cell: (row) => row?.name,
    },
    {
      name: <FormattedMessage id="country code" />,
      minWidth: "308px",
      selector: "code",
      sortable: false,
      cell: (row) => <span className="text-ellipse-1">{row?.code}</span>,
    },
    {
      name: <FormattedMessage id="phone code" />,
      minWidth: "308px",
      selector: "phone_code",
      sortable: false,
      cell: (row) => <span className="text-ellipse-1">{row?.phone_code}</span>,
    },

    {
      name: <FormattedMessage id="actions" />,
      minWidth: "100px",
      cell: (row) => (
        <UncontrolledDropdown>
          <DropdownToggle tag="div" className="btn btn-sm">
            <MoreVertical size={14} className="cursor-pointer" />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem
              tag={Link}
              to={`/apps/cms/location/country/edit/${row.id}`}
              className="w-100"
            >
              <Edit size={14} className="mr-50" />
              <span className="align-middle">
                <FormattedMessage id="edit" />
              </span>
            </DropdownItem>
            <DropdownItem
              tag={Link}
              to={`/apps/cms/location/city/list/${row.id}`}
              className="w-100"
            >
              <MapPin size={14} className="mr-50" />

              <span className="align-middle">
                <FormattedMessage id="city" />
              </span>
            </DropdownItem>
            <DropdownItem
              // tag={Link}
              // to={`/apps/cms/location/city/list/${row.id}`}
              className="w-100"
              onClick={() => {
                handleDelete({
                  id: row.id,
                  data: {
                    is_deleted: 1,
                  },
                });
              }}
            >
              <Trash2 size={14} className="mr-50" />

              <span className="align-middle">
                <FormattedMessage id="delete" />
              </span>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      ),
    },
  ];

  useEffect(() => {
    getCountry();

    setDisable(false);
  }, [currentPage]);
  const getCountry = () => {
    request
      .send({
        method: api.FIND_COUNTRY.method,
        path: api.FIND_COUNTRY.path,
        data: {
          filter: {},
          skip: (currentPage - 1) * rowsPerPage,
          limit: rowsPerPage,
          order: {
            key: "id",
            value: "ASC",
          },
        },
      })
      .then((result) => {
        if (result) {
          const { statusCode, data } = result;
          if (statusCode === 200) {
            setListCountry(data?.data);
            setCount(Math.ceil(data?.total / rowsPerPage));
          }
        }
      });
  };

  const addCountry = (data) => {
    request
      .send({
        method: api.CREATE_COUNTRY.method,
        path: api.CREATE_COUNTRY.path,
        data,
      })
      .then((result) => {
        if (result) {
          const { statusCode, data } = result;
          if (statusCode === 200) {
            toast.success(<FormattedMessage id={"successfully added new!"} />);
            setDisable(false);
            getCountry();
            toggleSidebar();
          } else if (statusCode == 400) {
            toast.warn(<FormattedMessage id={"Add new failure!"} />);
            setDisable(false);
          }
        }
      })
      .catch((result) => {
        setDisable(false);
      });
  };

  useEffect(() => {
    request
      .send({
        method: api.FIND_COUNTRY.method,
        path: api.FIND_COUNTRY.path,
        data: {
          filter: {},
          skip: 0,
          limit: 100,
          order: {
            key: "id",
            value: "ASC",
          },
        },
      })
      .then((result) => {
        if (result) {
          const { statusCode, data } = result;
          if (statusCode === 200) {
            setListCountryExport(data?.data);
          }
        }
      });
  }, []);

  const exportToExcel = () => {
    if (listCountryExport?.length > 0) {
      const data = listCountryExport;
      const cols = [
        { header: "ID", key: "id" },
        { header: "M quc gia", key: "code" },
        { header: "Tn quc gia", key: "name" },
        { header: "M vng in thoi quc t", key: "phone_code" },
      ];
      const mappedData = data.map((item) =>
        Object.fromEntries(
          Object.entries(item).map(([key, val]) => {
            return [cols.find((col) => col.key === key)?.header || key, val];
          })
        )
      );
      const worksheet = XLSX.utils.json_to_sheet(mappedData, {
        header: cols.map((col) => col.header),
      });
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Quc gia");
      XLSX.writeFile(workbook, "country.xlsx");
    }
  };

  return (
    <div className="app-user-list">
      <ExtensionsHeader title={<FormattedMessage id="country" />} />
      <Card>
        <DataTable
          noHeader
          subHeader
          responsive
          paginationServer
          noDataComponent={
            <div className="sc-fznWqX gnahTY">
              <div className="sc-AxjAm gIMaKV rdt_Table" role="table">
                <div className="sc-fzqARJ icdHOq">
                  <div
                    style={{
                      padding: "25px ",
                      textAlign: "center",
                      color: "black",
                      background: "white",
                    }}
                  >
                    <FormattedMessage id={"There are no records to display"} />
                  </div>
                </div>
              </div>
            </div>
          }
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          data={listCountry}
          subHeaderComponent={
            <CustomHeader
              toggleSidebar={toggleSidebar}
              data={store?.data}
              exportToExcel={exportToExcel}
            />
          }
        />
      </Card>
      {count > 0 && (
        <ReactPaginate
          previousLabel={""}
          nextLabel={""}
          pageCount={count || 0}
          activeClassName="active"
          forcePage={currentPage !== 0 ? currentPage - 1 : 0}
          onPageChange={(page) => {
            setCurrentPage(page.selected + 1);
          }}
          pageClassName={"page-item"}
          nextLinkClassName={"page-link"}
          nextClassName={"page-item next"}
          previousClassName={"page-item prev"}
          previousLinkClassName={"page-link"}
          pageLinkClassName={"page-link"}
          containerClassName={
            "pagination react-paginate justify-content-center my-2 pr-1"
          }
        />
      )}
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        disabled={disable}
        {...{ addCountry, disable, setDisable }}
      />
      <div className="mt-4"></div>
    </div>
  );
};

export default CountryList;
